*
{
    font-family: "Poppins";
}


.moviedetail
{
    background-color: black;
    position: relative;
    height:calc(100vh - 70px);
    width: 100vw;
    z-index: -40;
    overflow: hidden;

}

.backdropDetail{
    position: absolute;
    bottom: 0%;
    right: 0%;
    top: 0%;
    left: 0%;
    object-fit: cover;
    width: 100%;
    height: 100%;
    filter: blur(18px);
    z-index: -40;
}

.thumb
{
    margin-left: 20px;
    box-shadow: 3px 3px 20px black;
    width: calc(120px + 10vw);
    padding: 15px;
    border-radius: 20px;
}



.MovieDetailsContainer
{
    background: transparent;
   
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center ;
    text-align: center;
    justify-content: center;
    -webkit-backdrop-filter: blur(8px);
    backdrop-filter: blur(8px);
    padding: 40px;

}

.MovieDetail
{
    padding: 5px 20px;
    margin-left: 2vw;
    text-align: center;
 
    
}

.MovieDetail h1
{
    color: red;
    font-family: 'Poppins';
}

.MovieDetail p
{
    /* width: 540px; */
    font-family: 'Poppins';
    text-shadow: 1px 1px 1px black;
}

.Genres
{
 margin: 0% 5px;
 color: red;
 font-weight: 900;


