
html{
    scroll-behavior: smooth;
}

.Header
{
    height: 70px ;
    background-color: white;

    justify-content: space-between;
    flex-direction: row;
    display: flex;
    color: white;
    align-items: center;
    box-shadow: 1px 1px 1px whitesmoke;
    position: sticky;
    width: 100%;
    top: 0%;
    
    z-index: 1000;
    
} 


.fire {
    color: orange;
    
    animation:fireAnimation 1s alternate infinite ;
}

@keyframes fireAnimation {
    0%{ color: yellow; transform: rotate(22deg); }
    100%{color: orange;  transform: rotate(-22deg)}
    50%{color: red;   transform: rotate(-55deg)}
    
}


.logoImage
{
    width:40px;
    border-radius: 8px;
    margin-left: 20px;
  
}
.LogoText {
    color: black;
    font-family: '';
    font-size: calc(10px + 0.5vw);
}

.search {
    display: inline-flex;
    padding: 5px;
    align-items: center;
    
}
input {
    padding: 5px;
    margin: 2px;
  
    outline: none;
    border: none;
    border-bottom: 1px solid black;
}
.links
{
    width: 30%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 30px;
}
.linkItem 
{
    color: black;
    text-decoration: none;
    padding: 4px;
    margin: 4px;
    text-transform: uppercase;
}

.SearchIcon
{
    padding: 20px;

}


.linkItem
{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    
}

.linkItem::before
{
    content: '';
    position: absolute;
    bottom: 0px;
    right: 4px;
    background-color: black;
    width: 80%;;
    height: 2px;
    transform-origin: right;
    transform: scaleX(0);
  
    transition: 250ms ease;
}
.linkItem:hover::before{
    opacity: 1;
    transform: scaleX(1);
}


@media only screen and (max-width: 1000px) {
    .linkItem span {
      display: none;
    }
  }


  @media only screen and (max-width: 730px) {
    .linkItem  {
        margin: 3vw;}
    .links{
        padding: 0%;
        margin: auto;
        justify-content: center;
    }
  }