.movieCard{
    width: 290px;
    height: 450px;
    border-radius: 20px;
    padding: 20px;
    margin: 20px;
    cursor: pointer;
    z-index: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    transition: 250ms ease;
}



.movieCard:hover
{
    outline: 1px solid black;

    transform: scale(1.01);
}
.CardImg
{
    width: 90%;
    border-radius: 10px;
    object-fit: cover;
    height: 80%;
    z-index: -1;
    
}


.cardInfo{
    font-family: 'Poppins';
    display: flex;
    color: black;
    flex-direction: column;
    text-align: center;
    width: 100%;
}
h4{
    width: 100%;
}


@media only screen and (max-width: 730px) {
    .movieCard  {
  
      width: 150px;
      height: 300px;
      padding: 3%;
      margin: auto;
      

    }
  }